import React, { Component } from "react";

export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
        <section id="portfolio">
          <div className="row">
            <div className="twelve columns collapsed">
              <h1>Check Out Some of My Works.</h1>
              {/* portfolio-wrapper */}
              <div
                id="portfolio-wrapper"
                className="bgrid-quarters s-bgrid-thirds cf"
              >
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-01" alt="true">
                      <img alt="true" src="images/portfolio/GoodDog2_800.jpg" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>GoodDog</h5>
                          <p>MVP</p>
                        </div>
                      </div>
                      <div className="link-icon">
                        <i className="icon-plus" />
                      </div>
                    </a>
                  </div>
                </div>{" "}
                {/* item end */}
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-02" alt="true">
                      <img alt="true" src="images/portfolio/pats_800.jpg" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>PATS</h5>
                          <p>Past Project</p>
                        </div>
                      </div>
                      <div className="link-icon">
                        <i className="icon-plus" />
                      </div>
                    </a>
                  </div>
                </div>{" "}
                {/* item */}
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-03" alt="true">
                      <img alt="true" src="images/portfolio/engage_800.jpg" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>Engage</h5>
                          <p>OpenSource</p>
                        </div>
                      </div>
                      <div className="link-icon">
                        <i className="icon-plus" />
                      </div>
                    </a>
                  </div>
                </div>{" "}
                {/* item end*/}
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-04" alt="true">
                      <img
                        alt="true"
                        src="images/portfolio/emanhi.digital_800.jpg"
                      />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>Emanhi Digital</h5>
                          <p>Testing</p>
                        </div>
                      </div>
                      <div className="link-icon">
                        <i className="icon-plus" />
                      </div>
                    </a>
                  </div>
                </div>{" "}
                {/* item end*/}
              </div>{" "}
              {/* portfolio-wrapper end */}
            </div>{" "}
            {/* twelve columns end */}
            {/* Modal Popup
	      --------------------------------------------------------------- */}
            <div id="modal-01" className="popup-modal mfp-hide">
              <img
                className="scale-with-grid"
                src="images/portfolio/modals/m-gooddog_1050.jpg"
                alt="true"
              />
              <div className="description-box">
                <h4>GoodDog</h4>
                <p>
                  Minimal Viable Product Dog walking app. Submitted to
                  investors. Agile team project , Full Stack C#, JS, React, SQL,
                  SendGrid, Formik Unit Testing...
                </p>
                <span className="categories">
                  <i className="fa fa-tag" />
                  Responsive Web App
                </span>
              </div>
              <div className="link-box">
                <a href="http://www.gooddog.app">Details</a>
                <a className="popup-modal-dismiss">Close</a>
              </div>
            </div>
            {/* modal-01 End */}
            <div id="modal-02" className="popup-modal mfp-hide">
              <img
                className="scale-with-grid"
                src="images/portfolio/modals/m-pats_1050.jpg"
                alt="true"
              />
              <div className="description-box">
                <h4>Production Assistant Traning Seminars</h4>
                <p>
                  Education portal, National PA based for hiring and training
                  production assistants on location shoots Windows, Apache, PHP,
                  MySql, HTLM, WordPress, CSS
                </p>
                <span className="categories">
                  <i className="fa fa-tag" />
                  Web Development
                </span>
              </div>
              <div className="link-box">
                <a href="http://www.patrainingseminar.com">Details</a>
                <a className="popup-modal-dismiss">Close</a>
              </div>
            </div>
            {/* modal-02 End */}
            <div id="modal-03" className="popup-modal mfp-hide">
              <img
                className="scale-with-grid"
                src="images/portfolio/modals/m-engage_1050.jpg"
                alt="true"
              />
              <div className="description-box">
                <h4>Engage</h4>
                <p>
                  Engage is a tool that lets you easily follow issues your city
                  council is debating. More importantly, it provides a platform
                  for expressing your views on these issues, in a way you know
                  will be heard by your city. ReactJS
                </p>
                <span className="categories">
                  <i className="fa fa-tag" />
                  Outsource , HackForLA
                </span>
              </div>
              <div className="link-box">
                <a href="https://sm.engage.town">Details</a>
                <a className="popup-modal-dismiss">Close</a>
              </div>
            </div>
            {/* modal-Imani End */}
            <div id="modal-04" className="popup-modal mfp-hide">
              <img
                className="scale-with-grid"
                src="images/portfolio/modals/emanhi.digital.jpg"
                alt="true"
              />
              <div className="description-box">
                <h4>Emanhi Digital</h4>
                <p />
                <span className="categories">
                  <i className="fa fa-tag" />
                  Testing
                </span>
              </div>
              <div className="link-box">
                <a href="https://emanhi.net/techsolutions/">Details</a>
                <a className="popup-modal-dismiss">Close</a>
              </div>
            </div>
          </div>{" "}
          {/* row End */}
        </section>{" "}
        {/* Portfolio Section End */}
      </React.Fragment>
    );
  }
}
