import React, { Component } from "react";

export default class About extends Component {
  render() {
    return (
      <React.Fragment>
        <div>
          <section id="about">
            <div className="row">
              <div className="three columns">
                <img
                  className="profile-pic"
                  src="images/profilepic.jpg"
                  alt="true"
                />
              </div>
              <div className="nine columns main-col">
                <h2>About Me</h2>
                <p>
                  I offer a unique work experience and acumen in digital
                  technology from programming to networking IP. This background
                  is driven with can-do attitude, part analytical and creative
                  mind, and tenacious thirst for learning
                </p>
                <div className="row">
                  <div className="columns contact-details">
                    <a className="smoothscroll" href="#contact">
                      <h2>Contact Details</h2>
                    </a>
                  </div>
                  <div className="columns download">
                    <p>
                      <a
                        href="https://www.linkedin.com/in/ticoislinked/"
                        className="button"
                      >
                        <i className="fa fa-download" />
                        Download Resume
                      </a>
                    </p>
                  </div>
                </div>{" "}
                {/* end row */}
              </div>{" "}
              {/* end .main-col */}
            </div>
          </section>
        </div>
      </React.Fragment>
    );
  }
}
