import React, { Component } from "react";
import * as emailjs from "emailjs-com";
import swal from "sweetalert";

var user_id = "user_QHOWRJS1UsM98HEoLVmI4";
var templateId = "portfolio_email";
export default class ContactUs extends Component {
  state = {
    feedback: "",
    user_email: "",
    user_name: "",
    subject: "",
    data: {},

    formSubmitted: false,
    formEmailSent: false
  };
  baseState = this.state;

  // validation

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    });
  };

  handleSubmit = event => {
    event.preventDefault();

    const { user_email, user_name, subject, feedback } = this.state;

    this.sendFeedBack(
      templateId,
      user_id,
      feedback,
      user_email,
      user_name,
      subject
    );

    this.setState({ formSubmitted: true });
    this.setState(this.baseState);
  };
  sendFeedBack(templateId, user_id, user_email, user_name, subject, feedback) {
    emailjs
      .send(
        "mailgun",
        templateId,
        { user_email, user_name, subject, feedback },
        user_id
      )
      .then(response => {
        console.log("Success", response.status, response.text);
        swal({
          title: "Email Sent",
          text: "Thank you!",
          icon: "success"
        });
        this.resetForm();
      })
      .catch(err => {
        console.log("FAILED", err);
      });
  }

  render() {
    // const { user_email, user_name, feedback } = this.state;
    // const isEnabled = user_email.length > 0 && user_name > 0 && feedback > 0;
    // const validate = () => {};

    return (
      <React.Fragment>
        <section id="contact">
          <div className="row">
            <div className="eight columns center">
              <h2
                style={{
                  color: "white",
                  paddingBottom: 100
                }}
              >
                Feel Free to send me a message!
              </h2>{" "}
            </div>
            <div />
          </div>
          <div className="row">
            <div className="eight columns">
              {/* form */}
              <form
                className="feedback-form"
                onSubmit={event => this.handleSubmit(event)}
              >
                <fieldset>
                  <div>
                    <label htmlFor="contactName">
                      Name <span className="required" />
                    </label>
                    <input
                      type="text"
                      onChange={this.handleChange}
                      size={35}
                      id="user_name"
                      name="user_name"
                      value={this.state.user_name}
                    />
                  </div>
                  <div>
                    <label htmlFor="contactEmail">
                      Email <span className="required" />
                    </label>
                    <input
                      type="email"
                      onChange={this.handleChange}
                      size={35}
                      id="user_email"
                      name="user_email"
                      value={this.state.user_email}
                    />
                  </div>
                  <div>
                    <label htmlFor="subject"> Subject</label>
                    <input
                      type="text"
                      onChange={this.handleChange}
                      size={35}
                      id="subject"
                      name="subject"
                      value={this.state.subject}
                    />
                  </div>
                  <div>
                    <label htmlFor="contactMessage">
                      Message <span className="required">*</span>
                    </label>
                    <textarea
                      cols={50}
                      rows={15}
                      onChange={this.handleChange}
                      id="feedback"
                      name="feedback"
                      value={this.state.feedback}
                    />
                  </div>
                  <div>
                    <button className="submit">Submit</button>
                    <span id="image-loader">
                      <img alt="true" src="images/loader.gif" />
                    </span>
                  </div>
                </fieldset>
              </form>{" "}
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}
