import React, { Component } from "react";

export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
        <section id="testimonials">
          <div className="text-container">
            <div className="row">
              <div className="two columns header-col">
                <h1>
                  <span>Client Testimonials</span>
                </h1>
              </div>
              <div className="ten columns flex-container">
                <div className="flexslider">
                  <ul className="slides">
                    <li>
                      <blockquote>
                        <p>
                          “Tico is a strategic thinker who truly understands the
                          technical and client function. He is a creative
                          thinker who considers different perspectives to
                          improve outcomes and recognizes opportunities to
                          improve processes"
                        </p>
                        <cite>M. Chiquito</cite>
                      </blockquote>
                    </li>{" "}
                    {/* slide ends */}
                    <li>
                      <blockquote>
                        <p>
                          We have worked together on multiple technology
                          projects that included file organization, marketing
                          and resource allocation. Tico was very thorough in his
                          analysis. He researched multiple options, analyzes
                          which will work best and then makes his
                          recommendation. Working with Tico is a pleasure.
                        </p>
                        <cite>D. Wegner</cite>
                      </blockquote>
                    </li>{" "}
                    {/* slide ends */}
                  </ul>
                </div>{" "}
                {/* div.flexslider ends */}
              </div>{" "}
              {/* div.flex-container ends */}
            </div>{" "}
            {/* row ends */}
          </div>{" "}
          {/* text-container ends */}
        </section>{" "}
        {/* Testimonials Section End*/}
      </React.Fragment>
    );
  }
}
